export default {
	data() {
		return {
			cover:""
		}
	},
	onLoad(params){
		var cover = params.large;
		this.cover = cover;
		uni.setNavigationBarColor({
			frontColor:'#fff',
			backgroundColor:"#000"
		})
		console.log(this.cover);
	},
	methods: {
		postd(){
			uni.showActionSheet({  //​显示操作菜单
				itemList:['下载图片','分享'],
				success:(res) =>{
					if(res.tapIndex == 0){
						uni.showLoading({   //显示中间的加载框
							title:"图片保存中..."
						})
						uni.downloadFile({   //下载文件资源到本地，客户端直接发起一个 HTTP GET 请求，返回文件的本地临时路径。
							url:this.cover,
							success:function(result){
								var a = result.tempFilePath;
								uni.saveImageToPhotosAlbum({   // 保存到系统相册
									filePath:a,
									success:function(){
										uni.showToast({
											title:"保存成功",
											duration:2000
										})
									},
									complete:function(){
										uni.hideLoading();
									}
								})
								console.log(a);
							}
						})
					}
				}
			})
		}
	}
}
